import Popup from './Popup.js';

export default class PopupWithForm extends Popup {
  constructor({ handleFormSubmit }, popupSelector) {
    super(popupSelector);
    this._popupSelector = document.querySelector(popupSelector);
    this._form = this._popupSelector.querySelector('.popup__form');
    this.handleFormSubmit = handleFormSubmit;

  }
  _getInputValues() {
    // Ð´Ð¾ÑÑÐ°ÑÐ¼ Ð²ÑÐµ ÑÐ»ÐµÐ¼ÐµÐ½ÑÑ Ð¿Ð¾Ð»ÐµÐ¹
    this._inputList = this._form.querySelectorAll('.popup__input');
    // ÑÐ¾Ð·Ð´Ð°ÑÐ¼ Ð¿ÑÑÑÐ¾Ð¹ Ð¾Ð±ÑÐµÐºÑ
    this._formValues = {};
    // Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð² ÑÑÐ¾Ñ Ð¾Ð±ÑÐµÐºÑ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ñ Ð²ÑÐµÑ Ð¿Ð¾Ð»ÐµÐ¹
    this._inputList.forEach(input => {
      this._formValues[input.name] = input.value;

    });
    // Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÐ¼ Ð¾Ð±ÑÐµÐºÑ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ð¹
    return this._formValues;
  }

  setEventListeners() {
    super.setEventListeners();
    this._popupSelector.addEventListener('submit', (evt) => {
      evt.preventDefault();
      this.handleFormSubmit(this._getInputValues())
      this.close();
    })
  }
  close() {
    super.close();
    this._form.reset();
  }

  loading(loading) {
    if (loading) {
      this._form.querySelector('.popup__button').textContent = 'Ð¡Ð¾ÑÑÐ°Ð½ÐµÐ½Ð¸Ðµ...';
    } else {
      this._form.querySelector('.popup__button').textContent = 'Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ';
    }
  }

}
