export default class Section {
  constructor({ items, renderer }, containerSelector) {
    this._array = items;
    //ÑÑÐ½ÐºÑÐ¸Ñ, ÐºÐ¾ÑÐ¾ÑÐ°Ñ Ð¾ÑÐ²ÐµÑÐ°ÐµÑ Ð·Ð° ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ðµ Ð¸ Ð¾ÑÑÐ¸ÑÐ¾Ð²ÐºÑ Ð´Ð°Ð½Ð½ÑÑ Ð½Ð° ÑÑÑÐ°Ð½Ð¸ÑÐµ.
    this._renderer = renderer;
    //DOM-ÑÐ»ÐµÐ¼ÐµÐ½Ñ, Ð½Ð°Ð¹Ð´ÐµÐ½Ð½ÑÐ¹ Ð¿Ð¾ ÑÐµÐ»ÐµÐºÑÐ¾ÑÑ 
    this._container = document.querySelector(containerSelector)

  }
  // Ð²ÑÑÐ°Ð²Ð»ÑÐµÐ¼ element Ð¼ÐµÑÐ¾Ð´Ð¾Ð¼ append Ð² Ð¿Ð¾Ð»Ðµ _container
  addItem(element) {
    this._container.prepend(element);
  }

  setItem(element) {
    this._container.append(element);
  }

  //Ð¿ÐµÑÐµÐ±Ð¸ÑÐ°ÐµÑ Ð¼Ð°ÑÑÐ¸Ð² Ð´Ð°Ð½Ð½ÑÑ Ð¸ Ð´Ð»Ñ ÐºÐ°Ð¶Ð´Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ° Ð¼Ð°ÑÑÐ¸Ð²Ð° Ð²ÑÐ·ÑÐ²Ð°ÐµÑ renderer
  renderItems() {
    this._array.forEach((item) => {
      this._renderer(item);

    });
  };

}


