export class FormValidator {
  constructor(config, formElement) {
    this._formElement = formElement;
    this._formSelector = config.formSelector;
    this._inputSelector = config.inputSelector;
    this._submitButtonSelector = config.submitButtonSelector
    this._inactiveButtonClass = config.inactiveButtonClass;
    this._inputErrorClass = config.inputErrorClass;
    this._errorClass = config.errorClass;
  }

  // Ð¤ÑÐ½ÐºÑÐ¸Ñ, ÐºÐ¾ÑÐ¾ÑÐ°Ñ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ ÐºÐ»Ð°ÑÑ Ñ Ð¾ÑÐ¸Ð±ÐºÐ¾Ð¹
  _showError(inputElement, errorMessage) {
    const errorElement = this._formElement.querySelector(`#${inputElement.id}-error`);
    inputElement.classList.add(this._inputErrorClass);
    errorElement.textContent = errorMessage;
    // ÐÐ¾ÐºÐ°Ð·ÑÐ²Ð°ÐµÐ¼ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ðµ Ð¾Ð± Ð¾ÑÐ¸Ð±ÐºÐµ
    errorElement.classList.add(this._errorClass);

  };


  // Ð¤ÑÐ½ÐºÑÐ¸Ñ, ÐºÐ¾ÑÐ¾ÑÐ°Ñ ÑÐ´Ð°Ð»ÑÐµÑ ÐºÐ»Ð°ÑÑ Ñ Ð¾ÑÐ¸Ð±ÐºÐ¾Ð¹
  _hideError(inputElement) {
    const errorElement = this._formElement.querySelector(`#${inputElement.id}-error`);
    inputElement.classList.remove(this._inputErrorClass);
    // Ð¡ÐºÑÑÐ²Ð°ÐµÐ¼ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ðµ Ð¾Ð± Ð¾ÑÐ¸Ð±ÐºÐµ
    errorElement.classList.remove(this._errorClass);
    errorElement.textContent = '';

  };

  // Ð¤ÑÐ½ÐºÑÐ¸Ñ, ÐºÐ¾ÑÐ¾ÑÐ°Ñ Ð¿ÑÐ¾Ð²ÐµÑÑÐµÑ Ð²Ð°Ð»Ð¸Ð´Ð½Ð¾ÑÑÑ Ð¿Ð¾Ð»Ñ
  _checkInputValidity(inputElement) {
    if (!inputElement.validity.valid) {
      // ÐÑÐ»Ð¸ Ð¿Ð¾Ð»Ðµ Ð½Ðµ Ð¿ÑÐ¾ÑÐ¾Ð´Ð¸Ñ Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ñ, Ð¿Ð¾ÐºÐ°Ð¶ÐµÐ¼ Ð¾ÑÐ¸Ð±ÐºÑ
      this._showError(inputElement, inputElement.validationMessage);

    } else {
      // ÐÑÐ»Ð¸ Ð¿ÑÐ¾ÑÐ¾Ð´Ð¸Ñ, ÑÐºÑÐ¾ÐµÐ¼
      this._hideError(inputElement);
    }
  };

  // ÑÑÐ½ÐºÑÐ¸Ñ Ð¿ÐµÑÐµÐºÐ»ÑÑÐµÐ½Ð¸Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ 
  _toggleButtonState(inputList, buttonElement) {
    // ÐÑÐ»Ð¸ ÐµÑÑÑ ÑÐ¾ÑÑ Ð±Ñ Ð¾Ð´Ð¸Ð½ Ð½ÐµÐ²Ð°Ð»Ð¸Ð´Ð½ÑÐ¹ Ð¸Ð½Ð¿ÑÑ
    if (this._hasInvalidInput(inputList)) {
      // ÑÐ´ÐµÐ»Ð°Ð¹ ÐºÐ½Ð¾Ð¿ÐºÑ Ð½ÐµÐ°ÐºÑÐ¸Ð²Ð½Ð¾Ð¹
      buttonElement.classList.add('popup__button_inactive');
      buttonElement.setAttribute('disabled', true);
    } else {
      // Ð¸Ð½Ð°ÑÐµ ÑÐ´ÐµÐ»Ð°Ð¹ ÐºÐ½Ð¾Ð¿ÐºÑ Ð°ÐºÑÐ¸Ð²Ð½Ð¾Ð¹
      buttonElement.classList.remove('popup__button_inactive');
      buttonElement.removeAttribute('disabled');
    }
  };


  //ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ Ð²ÑÐµÑ ÑÐ¾ÑÐ¼
  _setEventListeners() {
    const inputList = Array.from(this._formElement.querySelectorAll(this._inputSelector));
    const buttonElement = this._formElement.querySelector(this._submitButtonSelector);
    this._toggleButtonState(inputList, buttonElement);
    inputList.forEach((inputElement) => {
      inputElement.addEventListener('input', () => {
        this._checkInputValidity(inputElement);
        this._toggleButtonState(inputList, buttonElement);

      });

    });
  }

  // Ð¤ÑÐ½ÐºÑÐ¸Ñ Ð¿ÑÐ¾Ð²ÐµÑÑÑÑÐ°Ñ Ð²Ð°Ð»Ð¸Ð´Ð½Ð¾ÑÑÑ Ð¿Ð¾Ð»ÐµÐ¹
  _hasInvalidInput(inputList) {
    // Ð¿ÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ Ð¿Ð¾ ÑÑÐ¾Ð¼Ñ Ð¼Ð°ÑÑÐ¸Ð²Ñ Ð¼ÐµÑÐ¾Ð´Ð¾Ð¼ some
    return inputList.some((inputElement) => {
      // ÐÑÐ»Ð¸ Ð¿Ð¾Ð»Ðµ Ð½Ðµ Ð²Ð°Ð»Ð¸Ð´Ð½Ð¾, ÐºÐ¾Ð»Ð±ÑÐº Ð²ÐµÑÐ½ÑÑ true
      // ÐÐ±ÑÐ¾Ð´ Ð¼Ð°ÑÑÐ¸Ð²Ð° Ð¿ÑÐµÐºÑÐ°ÑÐ¸ÑÑÑ Ð¸ Ð²ÑÑ ÑÑÐ½ÑÐºÑÐ¸Ñ
      // hasInvalidInput Ð²ÐµÑÐ½ÑÑ true
      return !inputElement.validity.valid;
    })
  };


  //ÑÐºÑÑÐ²Ð°ÐµÐ¼ Ð¾ÑÐ¸Ð±ÐºÐ¸ Ð¿ÑÐ¸ Ð¾ÑÐºÑÑÑÐ¸Ð¸
  openPopupAndHideErrors() {
    const inputList = Array.from(this._formElement.querySelectorAll(this._inputSelector));
    inputList.forEach((inputElement) => {
      this._hideError(inputElement);
      const buttonElement = this._formElement.querySelector(this._submitButtonSelector);
      this._toggleButtonState(inputList, buttonElement);
    });
  }

  // ÑÑÐ½ÐºÑÐ¸Ñ Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ð¸ ÑÐ¾ÑÐ¼Ñ Ð½Ð° Ð¾ÑÐ½Ð¾Ð²Ð°Ð½Ð¸Ð¸ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÐ¾Ð² Ð² Ð¾Ð±ÑÐµÐºÑÐµ
  enableValidation() {
    this._formElement.addEventListener('submit', (evt) => {
      evt.preventDefault();
    });
    this._setEventListeners();
  }
}  