export default class UserInfo {
  constructor({ userNameSelector, userJobSelector, userAvatar}) {
    this._userNameElement = document.querySelector(userNameSelector);
    this._userJobElement = document.querySelector(userJobSelector);
    this._userAvatar = document.querySelector(userAvatar);
    
  }

  //Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÑ Ð¾Ð±ÑÐµÐºÑ Ñ Ð´Ð°Ð½Ð½ÑÐ¼Ð¸ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ. Ð­ÑÐ¾Ñ Ð¼ÐµÑÐ¾Ð´ Ð¿ÑÐ¸Ð³Ð¾Ð´Ð¸ÑÑÑ ÐºÐ¾Ð³Ð´Ð° Ð´Ð°Ð½Ð½ÑÐµ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ Ð½ÑÐ¶Ð½Ð¾ Ð±ÑÐ´ÐµÑ Ð¿Ð¾Ð´ÑÑÐ°Ð²Ð¸ÑÑ Ð² ÑÐ¾ÑÐ¼Ñ Ð¿ÑÐ¸ Ð¾ÑÐºÑÑÑÐ¸Ð¸.
  getUserInfo() {
    this._userProfileData = {};
    this._userProfileData.name = this._userNameElement.textContent;
    this._userProfileData.job = this._userJobElement.textContent;
    return this._userProfileData;
  }

  getUserId() {
    return this._userId;
  }

  //Ð¿ÑÐ¸Ð½Ð¸Ð¼Ð°ÐµÑ Ð½Ð¾Ð²ÑÐµ Ð´Ð°Ð½Ð½ÑÐµ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ Ð¸ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ Ð¸Ñ Ð½Ð° ÑÑÑÐ°Ð½Ð¸ÑÑ
  setUserInfo(name, job, userId) {
    this._userNameElement.textContent = name;
    this._userJobElement.textContent = job;
    this._userId = userId;

  }

  setUserAvatar(src) {
    this._userAvatar.src = src;
}
}